
.step-tabs:not(.text) {
  text-align: center;
  transform: translateX(-0.18rem);
}
.step-tabs:not(.text) .tab {
  position: relative;
  display: inline-block;
  padding: 0.0825rem 0.5rem;
  color: #fff;
  background-color: rgba(255,255,255,0.2);
  border-color: rgba(255,255,255,0.2);
  margin-left: 0.21rem;
}
.step-tabs:not(.text) .tab:first-child {
  border-radius: 0.02rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.step-tabs:not(.text) .tab:not(:first-child):before {
  position: absolute;
  top: 0;
  left: -0.18rem;
  content: '';
  width: 0;
  height: 0;
  border-top: 0.185rem solid;
  border-bottom: 0.185rem solid;
  border-color: inherit;
  border-left: 0.185rem solid transparent;
  z-index: 1;
}
.step-tabs:not(.text) .tab:after {
  position: absolute;
  top: 0;
  left: 100%;
  content: '';
  width: 0;
  height: 0;
  border-top: 0.185rem solid transparent;
  border-bottom: 0.185rem solid transparent;
  border-left: 0.185rem solid;
  border-left-color: inherit;
  z-index: 1;
}
.step-tabs:not(.text) .tab.active {
  background-color: #1a9ff3;
  border-color: #1a9ff3;
}
.step-tabs.text {
  text-align: center;
  transform: translateX(-0.15rem);
}
.step-tabs.text .tab {
  position: relative;
  display: inline-block;
  padding: 0 0.7rem 0 0.5rem;
  color: #999;
  font-size: 0.24rem;
}
.step-tabs.text .tab.active {
  color: #1a9ff3;
}
.step-tabs.text .tab:last-child {
  background: none;
}
.step-tabs.text-small .tab {
  padding: 0 0.34rem 0 0.2rem;
  font-size: 0.2rem;
}
.step-tabs.text-small .ds-icon-right-arrow {
  background: url("$CASSETS/guide/right-arrow-small.png") right center no-repeat;
}


