
.game-luck-number-history {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  max-height: 4rem;
  transition: all linear 0.2s;
  max-width: 9.3rem;
  margin: 0 auto;
  padding: 0 0.15rem;
  overflow: auto;
  z-index: 1999;
  border-bottom-left-radius: 0.05rem;
  border-bottom-right-radius: 0.05rem;
}
.game-luck-number-history.show {
  height: calc(100% - 0.96rem);
  overflow: auto;
}
.game-luck-number-history.empty {
  display: none;
}
.game-luck-number-history .lucky-numbers {
  background-color: #fff;
  color: #aaa;
}
.game-luck-number-history .lucky-numbers > .left {
  min-width: 3rem;
  height: 0.7rem;
  line-height: 0.7rem;
}
.game-luck-number-history .lucky-numbers .NPER {
  color: #999;
  font-family: Roboto;
  font-size: 0.2rem;
  font-weight: bold;
  vertical-align: middle;
}


