header {
  position: absolute;
  top: 0;
  max-width: calc(100% - 1.5rem);
  left: 0;
  right: 0;
  height: 0.36rem;
  z-index: 3;
}
header + .scroll-content {
  top: 0.36rem;
}
#app:not(.classic) .dialog-container {
  z-index: 2;
}
.scroll-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
  overflow-scrolling: touch;
}
[class*=-enter],
[class*=-leave] {
  transition: all linear 0.2s;
}
.zoom-enter-active,
.zoom-leave-active {
  opacity: 0.2;
  transform: perspective(500px) translateZ(-1000px);
}
.slide-enter,
.slide-leave-active {
  opacity: 0;
  transform: translateX(100%);
}
.slide-up-enter,
.slide-up-leave-active {
  opacity: 0;
  transform: translateY(-100%);
}
.slide-down-enter,
.slide-down-leave-active {
  opacity: 0;
  transform: translateY(100%);
}
.slide-left-enter,
.slide-left-leave-active {
  opacity: 0;
  transform: translateX(-100%);
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.fade-enter.dialog-container,
.fade-leave-active.dialog-container {
  transition: all linear 0s;
}
.back>.page[class*=-leave] {
  transition-delay: 0s;
  z-index: 1;
}
.back>.page[class*=-enter] {
  transform: none;
}


