
.dices .dice {
  transition: color 0.25s;
  color: #666;
}
.dices.selected .dice {
  color: #fff;
}
.dices .dice::before,
.dices .dice::after {
  box-sizing: inherit;
  content: '';
  position: absolute;
  box-sizing: border-box;
  border: 2px solid transparent;
  width: 0;
  height: 0;
  border-radius: 0.1rem;
  z-index: 1;
  color: #ccc;
}
.dices .dice::before {
  top: 0;
  left: 0;
  border-top-color: transparent;
  border-right-color: transparent;
  transition: border-color 0.5s ease-out 0.5s, width 0.25s ease-out 0.75s, height 0.25s ease-out 0.5s;
}
.dices .dice::after {
  bottom: 0;
  right: 0;
  border-top-color: transparent;
  border-right-color: transparent;
  transition: border-color 0.5s ease-out, width 0.25s ease-out 0.25s, height 0.25s ease-out;
}
.dices.selected .dice::before,
.dices.selected .dice::after {
  width: 100%;
  height: 100%;
  color: #fff;
}
.dices.selected .dice::before {
  border-top-color: currentColor;
  border-right-color: currentColor;
  transition: width 0.25s ease-out, height 0.25s ease-out 0.25s;
}
.dices.selected .dice::after {
  border-bottom-color: currentColor;
  border-left-color: currentColor;
  transition: border-color 0s ease-out 0.5s, width 0.25s ease-out 0.5s, height 0.25s ease-out 0.75s;
}


